.customInput {
    color: inherit;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 5px 10px;

    &:hover {
        background-color: rgba(255,255,255,0.3);
    }

    &:focus {
        background-color: rgba(255,255,255,0.3);
        color: black;
    }

    &::placeholder {
        color: #ddd;
    }
}